* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scrollbar-width: thin; /*mozilla scrollbar*/
}

*::-webkit-scrollbar {
    width: .5vw;
    height: .5vw;
    background-color: #f0f0f0;
}

*::-webkit-scrollbar-thumb {
    background: #cdcdcd;
}

:root {
    --border-radius-val: .5em;
}

#root {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    height: 100vh;
    overflow: hidden;
    background: #679ECEFF;
    overflow-y: scroll;
}


.cv-content {
    display: flex;
    flex-direction: row;
    width: 60vw;
    background: white;

}

.cv-content__left-column, .cv-content__right-column {
    display: flex;
    flex-direction: column;
}

.cv-content__left-column {

    background-color: rgb(7, 59, 87);
    max-width: 380px;
    color: #000000;

}

.cv-content__right-column {
    width: 100%;

}


@media (max-width: 1200px) {
    .cv-content {
        width: 90vw;
    }

    *::-webkit-scrollbar {
        visibility: hidden;
    }
}

@media (max-width: 890px) {
    .cv-content {
        width: 100vw;
        flex-direction: column;
    }

    .cv-content__left-column, .cv-content__right-column {
        width: 100%;
        max-width: unset;
    }
}


